/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React from 'react';
import moment from 'moment';
import * as StringHelpers from 'Helpers/StringHelpers';
import { NodataIndicator } from 'Components/Utility';

const History = (props) => {
  return (
    <>
      <div className='row'>
        <div className='col-md-12 table-sm table-cm'>
          <div className='react-bootstrap-table scroll-table scroll-height-28'>
            <table className="table table-bordered">
              <thead>
                <tr>
                  <th style={{'width':'20%'}}>Date and Time</th>
                  <th style={{'width':'10%'}}>Invoice</th>
                  <th style={{'width':'10%'}}>Product</th>
                  <th style={{'width':'20%'}}>Description</th>
                  <th style={{'width':'10%'}}>Quantity</th>
                  <th style={{'width':'10%'}}>Price</th>
                  <th style={{'width':'10%'}}>Store Id</th>
                  <th style={{'width':'10%'}}>Serial#</th>
                </tr>
              </thead>
              <tbody>

              {props.Data.customerHistories?.length ?
                (props.Data.customerHistories.map((item, index) => {
                  return (
                    <tr key={index}>
                      <td style={{'width':'20%'}}>{moment(item.createdOn).format('MM/DD/YYYY hh:mm:ss a')}</td> 
                      <td style={{'width':'10%'}}>{item.invoiceNumber}</td>
                      <td style={{'width':'10%'}}>{item.itemCode}</td>
                      <td style={{'width':'20%'}}>{item.description}</td>
                      <td style={{'width':'10%'}}>{item.qty}</td>
                      <td style={{'width':'10%'}}>{StringHelpers.currencyFormat(item.price)}</td>
                      <td style={{'width':'10%'}}>{item.storeId}</td>
                      <td style={{'width':'10%'}}>{item.serialNo}</td>
                    </tr>
                  );
                })) : (
                  <tr>
                    <td colSpan="8" style={{ textAlign: 'center' }}><NodataIndicator /></td>
                  </tr>
                )
              }
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
};

export default History;